import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";

import Header from "../components/header/header";
import HomePage from "../pages/homePage/homePage";
import SemesterOne from "../pages/semesterOne/semesterOne";
import SemesterTwo from "../pages/semesterTwo/semesterTwo";
import SemesterThree from "../pages/semesterThree/semesterThree";
import SemesterFour from "../pages/semesterFour/semesterFour";
import SemesterFive from "../pages/semesterFive/semesterFive";
import SemesterSix from "../pages/semesterSix/semesterSix";
import SemesterSeven from "../pages/semesterSeven/semesterSeven";
import SemesterEight from "../pages/semesterEight/semesterEight";
import RIPage from "../pages/ripage/ripage";
import Random from "../pages/randomPage/randomPage";

const AppRouter = () => {
  return (
    <BrowserRouter>
      <React.Fragment>
        <Header />
        <Switch>
          <Route path="Stranger-Beings-DA/" component={HomePage} exact />
          <Route path="Stranger-Beings-DA/semester1" component={SemesterOne} />
          <Route path="Stranger-Beings-DA/semester2" component={SemesterTwo} />
          <Route
            path="Stranger-Beings-DA/semester3"
            component={SemesterThree}
          />
          <Route path="Stranger-Beings-DA/semester4" component={SemesterFour} />
          <Route path="Stranger-Beings-DA/semester5" component={SemesterFive} />
          <Route path="Stranger-Beings-DA/semester6" component={SemesterSix} />
          <Route
            path="Stranger-Beings-DA/semester7"
            component={SemesterSeven}
          />
          <Route
            path="Stranger-Beings-DA/semester8"
            component={SemesterEight}
          />
          <Route
            path="Stranger-Beings-DA/rural-internship"
            component={RIPage}
          />
          <Route path="Stranger-Beings-DA/random" component={Random} />
        </Switch>
      </React.Fragment>
    </BrowserRouter>
  );
};

export default AppRouter;
